
import _PropTypes from 'prop-types-internal';
import wrapType from './wrapType';

export const checkPropTypes = _PropTypes.checkPropTypes;

export const array = wrapType(_PropTypes['array'],'array');
export const bool = wrapType(_PropTypes['bool'],'bool');
export const func = wrapType(_PropTypes['func'],'func');
export const number = wrapType(_PropTypes['number'],'number');
export const object = wrapType(_PropTypes['object'],'object');
export const string = wrapType(_PropTypes['string'],'string');
export const symbol = wrapType(_PropTypes['symbol'],'symbol');
export const any = wrapType(_PropTypes['any'],'any');
export const arrayOf = wrapType(_PropTypes['arrayOf'],'arrayOf');
export const element = wrapType(_PropTypes['element'],'element');
export const elementType = wrapType(_PropTypes['elementType'],'elementType');
export const instanceOf = wrapType(_PropTypes['instanceOf'],'instanceOf');
export const node = wrapType(_PropTypes['node'],'node');
export const objectOf = wrapType(_PropTypes['objectOf'],'objectOf');
export const oneOf = wrapType(_PropTypes['oneOf'],'oneOf');
export const oneOfType = wrapType(_PropTypes['oneOfType'],'oneOfType');
export const shape = wrapType(_PropTypes['shape'],'shape');
export const exact = wrapType(_PropTypes['exact'],'exact');
