export const PropTypes = {    
array,
bool,
func,
number,
object,
string,
symbol,
any,
arrayOf,
element,
elementType,
instanceOf,
node,
objectOf,
oneOf,
oneOfType,
shape,
exact,
resetWarningCache,   
checkPropTypes 
}
//for compatibility
PropTypes.PropTypes = PropTypes;

export default PropTypes;    
